@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://use.typekit.net/ecw7swt.css);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://use.typekit.net/ecw7swt.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --background: #333;
  --event: #282828;
  --button: #789759;
  --text-color: #ccc;
  --body-font: "Abel", sans-serif;
  --header-font: "corn-dog", sans-serif;
}

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: #333;
  background-color: var(--background);
  color: #ccc;
  font-family: "Abel", sans-serif;
  font-family: var(--body-font);
  text-align: center;
}

.WelcomeScreen p {
  font-size: 18px;
  margin: 15px 0 15px 0;
  padding: 0% 5%;
}

.app-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2%;
  width: 25%;
  -webkit-animation: pulse 3s ease-in-out infinite alternate;
          animation: pulse 3s ease-in-out infinite alternate;
}

@-webkit-keyframes pulse {
  from {
    -webkit-filter: drop-shadow(0 0 15px #789759);
            filter: drop-shadow(0 0 15px #789759);
    -webkit-filter: drop-shadow(0 0 15px var(--button));
            filter: drop-shadow(0 0 15px var(--button));
  }
  to {
    -webkit-filter: drop-shadow(0 0 2px #789759);
            filter: drop-shadow(0 0 2px #789759);
    -webkit-filter: drop-shadow(0 0 2px var(--button));
            filter: drop-shadow(0 0 2px var(--button));
  }
}

@keyframes pulse {
  from {
    -webkit-filter: drop-shadow(0 0 15px #789759);
            filter: drop-shadow(0 0 15px #789759);
    -webkit-filter: drop-shadow(0 0 15px var(--button));
            filter: drop-shadow(0 0 15px var(--button));
  }
  to {
    -webkit-filter: drop-shadow(0 0 2px #789759);
            filter: drop-shadow(0 0 2px #789759);
    -webkit-filter: drop-shadow(0 0 2px var(--button));
            filter: drop-shadow(0 0 2px var(--button));
  }
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 2rem;
  background-color: #282828;
  background-color: var(--event);
  border-radius: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
}

.google-btn:hover {
  box-shadow: 0 0 7px #789759;
  box-shadow: 0 0 7px var(--button);
}

.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background-color: #282828;
  background-color: var(--event);
  border: none;
  outline: none;
  cursor: pointer;
  transition-duration: 0.4s;
}

.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  color: #ccc;
  color: var(--text-color);
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Abel";
  background-color: #282828;
  background-color: var(--event);
  width: 80%;
  height: 100%;
  text-align: center;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  transition-duration: 0.4s;
}

a {
  color: #789759;
  color: var(--button);
}

:root {
  --background: #333;
  --event: #282828;
  --button: #789759;
  --text-color: #ccc;
  --body-font: "Abel", sans-serif;
  --header-font: "corn-dog", sans-serif;
}

body {
  background-color: #333;
  background-color: var(--background);
  color: #ccc;
  font-family: "Abel", sans-serif;
  font-family: var(--body-font);
}

.App * {
  box-sizing: border-box;
}

h1 {
  font-family: "corn-dog", sans-serif;
  font-family: var(--header-font);
  font-weight: 400;
  font-style: normal;
  text-align: center;
  text-shadow: 1px 1px 2px #000000;
  padding: 2% 5%;
}

.filter {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 0.5em;
  gap: 0.5em;
  padding: 0% 5%;
}

.EventList {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-gap: 1.5em;
  gap: 1.5em;
  padding: 0% 5%;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 10px 0 10px 0;
}

.filter p {
  margin: 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  color: #ccc;
  color: var(--text-color);
  font-size: 12px;
  background-color: #282828;
  background-color: var(--event);
  padding: 8px 6px 8px 10px;
  margin: 1%;
  border-radius: 5px;
  outline: none;
  border: none;
  line-height: 20px;
  align-self: center;
  height: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.35);
}

.city {
  width: 250px;
}

.InputWrapper {
  position: relative;
  display: inline-block;
}

.suggestions {
  opacity: 0.98;
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0.5%;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #282828;
  background-color: var(--event);
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.suggestions > li:hover {
  background-color: #789759;
  background-color: var(--button);
}

.Event {
  background-color: #282828;
  background-color: var(--event);
  padding: 10px 20px 35px;
  text-align: left;
  border-radius: 10px;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.35);
}

.Event > h3 {
  text-shadow: 1px 1px 2px #000000;
}

.Details > h3 {
  text-shadow: 1px 1px 2px #000000;
}

.Description {
  margin-bottom: 10px;
}

.Calendar-link {
  color: #789759;
  color: var(--button);
}

.Calendar-link:hover {
  opacity: 0.8;
}

.Hide-details,
.Show-details {
  background-color: #789759;
  background-color: var(--button);
  color: #ccc;
  color: var(--text-color);
  position: absolute;
  right: 15px;
  bottom: 15px;
  padding: 5px 8px;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  transition-duration: 0.4s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
}

.Hide-details:hover,
.Show-details:hover {
  opacity: 0.8;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.NumberOfEvents {
  text-align: end;
}

@media (max-width: 675px) {
  .NumberOfEvents {
    text-align: center;
  }
  .NumberOfEvents > label {
    display: inline-block;
  }
  .filter {
    text-align: center;
  }
  .suggestions {
    text-align: center;
  }
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

:root {
  --button: #789759;
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #789759;
  background: var(--button);

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #789759, 0 0 5px #789759;
  box-shadow: 0 0 10px var(--button), 0 0 5px var(--button);
  opacity: 1;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #789759;
  border-top-color: var(--button);
  border-left-color: #789759;
  border-left-color: var(--button);
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

